<template>
    <div class="div-container">
        <el-container>
            <el-header><h2 class="login-title">Welcome To Litluecat Blogs</h2></el-header>
            <el-container>
                <el-aside width="60%" style="padding-left: 20px;padding-top: 20px">
                    <el-container>
                        <el-aside width="40%">
                            <div class="block" style="text-align: left;">
                                <el-timeline color="rgb">
                                    <el-scrollbar height="500px">
                                        <el-timeline-item v-for="(item,index) in dailyEvents" :key="index" :timestamp="item.time" placement="top">
                                            <h4 @click="initDailyEvent(item)">{{item.title}}</h4>
                                        </el-timeline-item>
                                    </el-scrollbar>
                                </el-timeline>
                            </div>
                        </el-aside>
                        <el-main class="daily_event_content">
                            <el-scrollbar height="500px">
                                <h3>{{this.dailyEventTitle}}</h3>
                                <el-link type="primary" :href="this.dailyEventUrl" target="_blank">查看具体内容</el-link>
                                <pre>{{this.dailyEventContent}}</pre>
                            </el-scrollbar>
                        </el-main>
                    </el-container>
                </el-aside>
                <el-main>
                    <router-link to="/index"><h3><i class="el-icon-view"></i>&nbsp;&nbsp;I am just looking.（游客进入主页）</h3></router-link>
                    <h3>I am Litluecat. I have key.&nbsp;&nbsp;<i class="el-icon-key"></i></h3>
                    <el-form ref="form" :model="form" label-width="80px" class="login-form" size="mini">
                    <div style="margin-bottom: 10px"><el-avatar><img src="../assets/1.jpg"></el-avatar><i class="el-icon-lock"></i></div>
                    <el-form-item label="账号">
                        <el-input v-model="form.username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码">
                        <el-input type="password" v-model="form.password"></el-input>
                    </el-form-item>
                    <el-button type="primary" size="mini" @click="onSubmit"><i class="el-icon-unlock"></i>登录</el-button>
                    </el-form>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import md5 from 'js-md5'
import _dailyEvents from '../textlibrary/dailyEvents'

export default {
    name: 'login',
    data() {
        return {
            form: {
                username: "",
                password: ""
            },
            dailyEvents: [],
            dailyEventTitle: '点击左侧新闻，查看相关内容',
            dailyEventContent: '',
            dailyEventUrl: '',
        };
    },
    created() {
        this.dailyEvents = _dailyEvents;
    },
    methods: {
        onSubmit() {//maomao mimi
            // eslint-disable-next-line no-empty
            if("e2145387bcc7842edef4ccecc34b7da4" == md5(this.form.username) &&
                // eslint-disable-next-line no-empty
                "dde6ecd6406700aa000b213c843a3091" == md5(this.form.password)){
                this.$router.push({name:'Index', params:{username: 'maomao'}})
            }
        },
        initDailyEvent(dailyEvent){
            this.dailyEventTitle = dailyEvent.title
            this.dailyEventContent = dailyEvent.content
            this.dailyEventUrl = dailyEvent.url
        }
    }
};
</script>

<style acoped>
.daily_event_content pre {
    white-space: pre-wrap; /*css-3*/
    white-space: -moz-pre-wrap; /*Mozilla,since1999*/
    white-space: -pre-wrap; /*Opera4-6*/
    white-space: -o-pre-wrap; /*Opera7*/
    word-wrap: break-word; /*InternetExplorer5.5+*/
    width: 90%;
    text-align: left;
    height: 500px;
    line-height: 20px;
}
.login-form {
    width: 350px;
    margin: 80px auto; /* 上下间距160px，左右自动居中*/
    background-color: rgba(255, 255, 255, 0.3); /* 透明背景色 */
    padding: 30px;
    border-radius: 20px; /* 圆角 */
}

/* 标题 */
.login-title {
    color: #303133;
    text-align: center;
}
</style>
