    <p>背景：最近需要用到人脸识别，但又不花钱使用现有的第三方人脸识别接口，为此使用opencv结合java进行人脸识别（ps:opencv是开源的，使用它来做人脸识别存在一定的误差，效果一般）。</p>
    <ol><li>安装opencv<br> 官网地址：<a href="https://opencv.org/">https://opencv.org/</a>, 由于官网下载速度是真的慢<br> 为此这边是我下的<a href="https://pan.baidu.com/s/1MWTAo92cYa4B2yYu4LNWKg">百度云盘，opencv4.1.0</a>，提取码1o36。<br> 如果是官网下载，就无脑安装就行了，安装完毕后。</li></ol>
    <p>将图一的两个文件复制到图二中。<br> <img src="https://img-blog.csdnimg.cn/2020102318074258.png?x-oss-process=image/watermark,type_ZmFuZ3poZW5naGVpdGk,shadow_10,text_aHR0cHM6Ly9ibG9nLmNzZG4ubmV0L0JsdWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center" alt="在这里插入图片描述"><br> <img src="https://img-blog.csdnimg.cn/20201023180855524.png?x-oss-process=image/watermark,type_ZmFuZ3poZW5naGVpdGk,shadow_10,text_aHR0cHM6Ly9ibG9nLmNzZG4ubmV0L0JsdWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center" alt="在这里插入图片描述"><br> 从我网盘下载的，忽略这些。</p>
