    <ol><li> <p>导入库依赖<br> File --&gt; Project Structure，点击Modules，选择需要使用opencv.jar的项目。<br> <img src="https://img-blog.csdnimg.cn/20201023181449543.png?x-oss-process=image/watermark,type_ZmFuZ3poZW5naGVpdGk,shadow_10,text_aHR0cHM6Ly9ibG9nLmNzZG4ubmV0L0JsdWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center" alt="dWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center)"><br> <img src="https://img-blog.csdnimg.cn/20201023181516871.png?x-oss-process=image/watermark,type_ZmFuZ3poZW5naGVpdGk,shadow_10,text_aHR0cHM6Ly9ibG9nLmNzZG4ubmV0L0JsdWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center" alt="在这里插入图片描述"><br> 选择直接opencv安装路径<br> <img src="https://img-blog.csdnimg.cn/20201023181605495.png?x-oss-process=image/watermark,type_ZmFuZ3poZW5naGVpdGk,shadow_10,text_aHR0cHM6Ly9ibG9nLmNzZG4ubmV0L0JsdWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center" alt="在这里插入图片描述"><br> <img src="https://img-blog.csdnimg.cn/20201023181718356.png?x-oss-process=image/watermark,type_ZmFuZ3poZW5naGVpdGk,shadow_10,text_aHR0cHM6Ly9ibG9nLmNzZG4ubmV0L0JsdWVyQ2F0,size_16,color_FFFFFF,t_70#pic_center" alt="在这里插入图片描述"></p> </li><li> <p>java代码demo</p> </li></ol>
    <pre class="prettyprint"><code class="prism language-java has-numbering" onclick="mdcp.copyCode(event)" style="position: unset;"><span class="token keyword">package</span> org<span class="token punctuation">.</span>Litluecat<span class="token punctuation">.</span>utils<span class="token punctuation">;</span>

    <span class="token keyword">import</span> org<span class="token punctuation">.</span>apache<span class="token punctuation">.</span>commons<span class="token punctuation">.</span>lang<span class="token punctuation">.</span>StringUtils<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>core<span class="token punctuation">.</span>*<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>highgui<span class="token punctuation">.</span>HighGui<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>highgui<span class="token punctuation">.</span>ImageWindow<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>imgcodecs<span class="token punctuation">.</span>Imgcodecs<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>imgproc<span class="token punctuation">.</span>Imgproc<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>objdetect<span class="token punctuation">.</span>CascadeClassifier<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>videoio<span class="token punctuation">.</span>VideoCapture<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>videoio<span class="token punctuation">.</span>VideoWriter<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>opencv<span class="token punctuation">.</span>videoio<span class="token punctuation">.</span>Videoio<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>slf4j<span class="token punctuation">.</span>Logger<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>slf4j<span class="token punctuation">.</span>LoggerFactory<span class="token punctuation">;</span>

    <span class="token keyword">import</span> java<span class="token punctuation">.</span>util<span class="token punctuation">.</span>Arrays<span class="token punctuation">;</span>

    <span class="token comment">/**
     * 人脸比对工具类
     * @author Litluecat
     * @Title: Opencv 图片人脸识别、实时摄像头人脸识别
    **/</span>
    <span class="token keyword">public</span> <span class="token keyword">class</span> <span class="token class-name">FaceVideo</span> <span class="token punctuation">{<!-- --></span>

        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">final</span> Logger log <span class="token operator">=</span> LoggerFactory<span class="token punctuation">.</span><span class="token function">getLogger</span><span class="token punctuation">(</span>FaceVideo<span class="token punctuation">.</span><span class="token keyword">class</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">final</span> String endImgUrl <span class="token operator">=</span> <span class="token string">"C:\\Users\\lenovo\\Desktop\\"</span><span class="token punctuation">;</span>
        <span class="token comment">/**
         * opencv的人脸识别xml文件路径
         */</span>
        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">final</span> String faceDetectorXML2URL <span class="token operator">=</span> <span class="token string">"D:\\Sofeware\\opencv\\sources\\data\\haarcascades\\haarcascade_frontalface_alt.xml"</span><span class="token punctuation">;</span>
        <span class="token comment">/**
         * opencv的人眼识别xml文件路径
         */</span>
        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">final</span> String eyeDetectorXML2URL <span class="token operator">=</span> <span class="token string">"D:\\Sofeware\\opencv\\sources\\data\\haarcascades\\haarcascade_eye.xml"</span><span class="token punctuation">;</span>
        <span class="token comment">/**
         * 直方图大小，越大精度越高，运行越慢
         */</span>
        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">int</span> Matching_Accuracy <span class="token operator">=</span> <span class="token number">100000</span><span class="token punctuation">;</span>
        <span class="token comment">/**
         * 初始化人脸探测器
         */</span>
        <span class="token keyword">private</span> <span class="token keyword">static</span> CascadeClassifier faceDetector<span class="token punctuation">;</span>
        <span class="token comment">/**
         * 初始化人眼探测器
         */</span>
        <span class="token keyword">private</span> <span class="token keyword">static</span> CascadeClassifier eyeDetector<span class="token punctuation">;</span>

        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">int</span> i<span class="token operator">=</span><span class="token number">0</span><span class="token punctuation">;</span>

        <span class="token keyword">static</span> <span class="token punctuation">{<!-- --></span>
            System<span class="token punctuation">.</span><span class="token function">loadLibrary</span><span class="token punctuation">(</span>Core<span class="token punctuation">.</span>NATIVE_LIBRARY_NAME<span class="token punctuation">)</span><span class="token punctuation">;</span>
            faceDetector <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">CascadeClassifier</span><span class="token punctuation">(</span>faceDetectorXML2URL<span class="token punctuation">)</span><span class="token punctuation">;</span>
            eyeDetector <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">CascadeClassifier</span><span class="token punctuation">(</span>eyeDetectorXML2URL<span class="token punctuation">)</span><span class="token punctuation">;</span>
        <span class="token punctuation">}</span>

        <span class="token keyword">public</span> <span class="token keyword">static</span> <span class="token keyword">void</span> <span class="token function">main</span><span class="token punctuation">(</span>String<span class="token punctuation">[</span><span class="token punctuation">]</span> args<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
            log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"开始人脸匹配"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token keyword">long</span> begin <span class="token operator">=</span> System<span class="token punctuation">.</span><span class="token function">currentTimeMillis</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token comment">// 1- 从摄像头实时人脸识别，识别成功保存图片到本地</span>
            <span class="token keyword">try</span><span class="token punctuation">{<!-- --></span>
                <span class="token function">getVideoFromCamera</span><span class="token punctuation">(</span>endImgUrl <span class="token operator">+</span> <span class="token string">"2.png"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                <span class="token comment">//仅用于强制抛异常，从而关闭GUI界面</span>
                Thread<span class="token punctuation">.</span><span class="token function">sleep</span><span class="token punctuation">(</span><span class="token number">1000</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                <span class="token keyword">int</span> err <span class="token operator">=</span> <span class="token number">1</span><span class="token operator">/</span><span class="token number">0</span><span class="token punctuation">;</span>

                <span class="token comment">// 2- 比对本地2张图的人脸相似度 （越接近1越相似）</span>
    <span class="token comment">//            double compareHist = FaceVideo.compare_image(endImgUrl + "test1.png" , endImgUrl + "face.png");</span>
    <span class="token comment">//            log.info("匹配度：{}",compareHist);</span>
    <span class="token comment">//            if (compareHist &gt; 0.72) {<!-- --></span>
    <span class="token comment">//                log.info("人脸匹配");</span>
    <span class="token comment">//            } else {<!-- --></span>
    <span class="token comment">//                log.info("人脸不匹配");</span>
    <span class="token comment">//            }</span>

            <span class="token punctuation">}</span><span class="token keyword">catch</span> <span class="token punctuation">(</span><span class="token class-name">Exception</span> e<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
                log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"开始强制关闭"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"人脸匹配结束，总耗时：{}ms"</span><span class="token punctuation">,</span><span class="token punctuation">(</span>System<span class="token punctuation">.</span><span class="token function">currentTimeMillis</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token operator">-</span>begin<span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                System<span class="token punctuation">.</span><span class="token function">exit</span><span class="token punctuation">(</span><span class="token number">0</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span>
        <span class="token punctuation">}</span>


        <span class="token comment">/**
         * OpenCV-4.1.1 从摄像头实时读取
         * @param targetImgUrl 比对身份证图片
         * @return: void
         * @date: 2019年8月19日 17:20:13
         */</span>
        <span class="token keyword">public</span> <span class="token keyword">static</span> <span class="token keyword">void</span> <span class="token function">getVideoFromCamera</span><span class="token punctuation">(</span>String targetImgUrl<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
