        <span class="token keyword">public</span> <span class="token keyword">static</span> Mat <span class="token function">getFace</span><span class="token punctuation">(</span>Mat image<span class="token punctuation">,</span> String targetImgUrl<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
