            Rect<span class="token punctuation">[</span><span class="token punctuation">]</span> rects<span class="token operator">=</span>face<span class="token punctuation">.</span><span class="token function">toArray</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"匹配到 "</span><span class="token operator">+</span>rects<span class="token punctuation">.</span>length<span class="token operator">+</span><span class="token string">" 个人脸"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token keyword">if</span><span class="token punctuation">(</span>rects <span class="token operator">!=</span> null <span class="token operator">&amp;&amp;</span> rects<span class="token punctuation">.</span>length <span class="token operator">&gt;=</span> <span class="token number">1</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
                i<span class="token operator">++</span><span class="token punctuation">;</span>
                <span class="token keyword">if</span><span class="token punctuation">(</span>i<span class="token operator">==</span><span class="token number">3</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
                    <span class="token comment">// 获取匹配成功第3次的照片</span>
                    Imgcodecs<span class="token punctuation">.</span><span class="token function">imwrite</span><span class="token punctuation">(</span>endImgUrl <span class="token operator">+</span> <span class="token string">"face.png"</span><span class="token punctuation">,</span> image<span class="token punctuation">)</span><span class="token punctuation">;</span>
