                    <span class="token keyword">new</span> <span class="token class-name">Thread</span><span class="token punctuation">(</span>faceVideoThread<span class="token punctuation">,</span><span class="token string">"人脸比对线程"</span><span class="token punctuation">)</span><span class="token punctuation">.</span><span class="token function">start</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
