         */</span>
        <span class="token keyword">public</span> <span class="token keyword">static</span> String <span class="token function">face2Img</span><span class="token punctuation">(</span>String img<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
            String faceImg <span class="token operator">=</span> null<span class="token punctuation">;</span>
