            <span class="token comment">// rect中人脸图片的范围</span>
            <span class="token keyword">for</span> <span class="token punctuation">(</span>Rect rect <span class="token operator">:</span> faceDetections<span class="token punctuation">.</span><span class="token function">toArray</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
