        <span class="token keyword">public</span> <span class="token keyword">static</span> <span class="token keyword">double</span> <span class="token function">compare_image</span><span class="token punctuation">(</span>String img_1<span class="token punctuation">,</span> String img_2<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
            Mat mat_1 <span class="token operator">=</span> <span class="token function">conv_Mat</span><span class="token punctuation">(</span>img_1<span class="token punctuation">)</span><span class="token punctuation">;</span>
            Mat mat_2 <span class="token operator">=</span> <span class="token function">conv_Mat</span><span class="token punctuation">(</span>img_2<span class="token punctuation">)</span><span class="token punctuation">;</span>
