            Imgproc<span class="token punctuation">.</span><span class="token function">calcHist</span><span class="token punctuation">(</span>Arrays<span class="token punctuation">.</span><span class="token function">asList</span><span class="token punctuation">(</span>mat_1<span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token keyword">new</span> <span class="token class-name">MatOfInt</span><span class="token punctuation">(</span><span class="token number">0</span><span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token keyword">new</span> <span class="token class-name">Mat</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">,</span> hist_1<span class="token punctuation">,</span> histSize<span class="token punctuation">,</span> ranges<span class="token punctuation">)</span><span class="token punctuation">;</span>
