            <span class="token keyword">double</span> res <span class="token operator">=</span> Imgproc<span class="token punctuation">.</span><span class="token function">compareHist</span><span class="token punctuation">(</span>hist_1<span class="token punctuation">,</span> hist_2<span class="token punctuation">,</span> Imgproc<span class="token punctuation">.</span>CV_COMP_CORREL<span class="token punctuation">)</span><span class="token punctuation">;</span>
