    <p>这边的人脸识别是另外其线程进行比对，代码如下。</p>
    <pre class="prettyprint"><code class="prism language-java has-numbering" onclick="mdcp.copyCode(event)" style="position: unset;"><span class="token keyword">package</span> org<span class="token punctuation">.</span>Litluecat<span class="token punctuation">.</span>utils<span class="token punctuation">;</span>

    <span class="token keyword">import</span> org<span class="token punctuation">.</span>slf4j<span class="token punctuation">.</span>Logger<span class="token punctuation">;</span>
    <span class="token keyword">import</span> org<span class="token punctuation">.</span>slf4j<span class="token punctuation">.</span>LoggerFactory<span class="token punctuation">;</span>


    <span class="token keyword">public</span> <span class="token keyword">class</span> <span class="token class-name">FaceVideoThread</span> <span class="token keyword">implements</span> <span class="token class-name">Runnable</span><span class="token punctuation">{<!-- --></span>
        <span class="token keyword">private</span> <span class="token keyword">static</span> <span class="token keyword">final</span> Logger log <span class="token operator">=</span> LoggerFactory<span class="token punctuation">.</span><span class="token function">getLogger</span><span class="token punctuation">(</span>FaceVideoThread<span class="token punctuation">.</span><span class="token keyword">class</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

        <span class="token keyword">private</span> String oneImgUrl <span class="token operator">=</span> null<span class="token punctuation">;</span>
        <span class="token keyword">private</span> String otherImgUrl <span class="token operator">=</span> null<span class="token punctuation">;</span>
        <span class="token keyword">public</span> <span class="token function">FaceVideoThread</span><span class="token punctuation">(</span>String oneImgUrl<span class="token punctuation">,</span> String otherImgUrl<span class="token punctuation">)</span><span class="token punctuation">{<!-- --></span>
            <span class="token keyword">this</span><span class="token punctuation">.</span>oneImgUrl <span class="token operator">=</span> oneImgUrl<span class="token punctuation">;</span>
            <span class="token keyword">this</span><span class="token punctuation">.</span>otherImgUrl <span class="token operator">=</span> otherImgUrl<span class="token punctuation">;</span>
        <span class="token punctuation">}</span>
        <span class="token annotation punctuation">@Override</span>
        <span class="token keyword">public</span> <span class="token keyword">void</span> <span class="token function">run</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
            <span class="token keyword">try</span> <span class="token punctuation">{<!-- --></span>
                <span class="token keyword">double</span> compareHist <span class="token operator">=</span> FaceVideo<span class="token punctuation">.</span><span class="token function">compare_image</span><span class="token punctuation">(</span>oneImgUrl <span class="token punctuation">,</span> otherImgUrl<span class="token punctuation">)</span><span class="token punctuation">;</span>
                log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"匹配度：{}"</span><span class="token punctuation">,</span>compareHist<span class="token punctuation">)</span><span class="token punctuation">;</span>
                <span class="token keyword">if</span> <span class="token punctuation">(</span>compareHist <span class="token operator">&gt;</span> <span class="token number">0.72</span><span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
                    log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"人脸匹配"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                <span class="token punctuation">}</span> <span class="token keyword">else</span> <span class="token punctuation">{<!-- --></span>
                    log<span class="token punctuation">.</span><span class="token function">info</span><span class="token punctuation">(</span><span class="token string">"人脸不匹配"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                <span class="token punctuation">}</span>
            <span class="token punctuation">}</span> <span class="token keyword">catch</span> <span class="token punctuation">(</span><span class="token class-name">Exception</span> e<span class="token punctuation">)</span> <span class="token punctuation">{<!-- --></span>
                e<span class="token punctuation">.</span><span class="token function">printStackTrace</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            <span class="token punctuation">}</span>
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span>

